'use client'

import { ThemeProvider as NextThemesProvider } from 'next-themes'
import * as React from 'react'

type Theme = 'light' | 'dark' | 'system'

export default function ThemeProvider({
  children,
  defaultTheme = 'light',
  ...props
}: React.ComponentProps<typeof NextThemesProvider> & { defaultTheme?: Theme }) {
  return (
    <NextThemesProvider attribute="class" defaultTheme={defaultTheme} disableTransitionOnChange {...props}>
      {children}
    </NextThemesProvider>
  )
}
